.mainTitle {
  position: fixed;
  display: flex;
  flex-direction: row;
}
.mainTitleSphere {
  background: #ffffff80;
  border-radius: 25px;
  box-shadow: 0 0 13px 4px #ffffff80;
  width: 200px;
  min-width: 200px;
  height: 200px;
}
.mainTitleText {
  background: #ffffff80;
  border-radius: 25px;
  box-shadow: 0 0 13px 4px #ffffff80;
  width: 600px;
  min-width: 600px;
  height: 100px;
  object-fit: cover;
  margin-top: 50px;
}

@media (max-width: 768px) {
  .mainTitle {
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 175px;

    background: #ffffff;
    z-index: 2;
  }
  .mainTitleSphere {
    min-width: initial;
    width: 100%;
    max-width: 75px;
    height: 75px;
  }
  .mainTitleText {
    min-width: initial;
    width: 100%;
    max-width: 400px;

    height: 75px;

    margin: 0;
    padding: 0 20px;
  }
}
